<template>
  <div class="login-wrapper">
    <h1>Logon to Agentily</h1>
    <yd-cell-group>
        <yd-cell-item>
            <span slot="left">Username: </span>
            <yd-input slot="right" required v-model="username" max="20" placeholder="username"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
            <span slot="left">Password: </span>
            <yd-input slot="right" type="password" v-model="password" placeholder="Password please"></yd-input>
        </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script>
export default {
  data () {
    return {
      username:'',
      password:''
    }
  }
}
</script>

<style lang="stylus" scoped>

</style>




